<template>
    <div class="layout">
        <Layout>
            <my-head @back="back"></my-head>
            <Content :style="{padding: '0 50px'}">
                <Row type="flex" justify="center" align="middle" class="code-row-bg">
                    <Col span="22" push="2" type="flex" justify="end">
                    <Row type="flex" justify="end" class="code-row-bg">
                        <DatePicker :editable="false" @on-change="setime" format="yyyy-MM-dd" type="daterange" lacement="bottom-end"
                                    placeholder="Select date"
                                    style="width: 180px"></DatePicker>
                        <Select v-model="model1" style="width:100px;margin: 0 20px;">
                            <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}
                            </Option>
                        </Select>
                        <Input v-model="SearchCon" placeholder="Enter Content..." style="width: 220px"></Input>
                        <Button @click="search" type="info" icon="ios-search" style="margin: 0 20px">Search</Button>
                        <Checkbox @on-change="Checkbox" label="twitter" style="padding-top: 7px;">
                            <span>只看待上架</span>
                        </Checkbox>
                    </Row>
                    </Col>
                    <Col span="2" pull="22">
                    <Breadcrumb :style="{margin: '20px 0'}">
                        <BreadcrumbItem>入库单</BreadcrumbItem>
                    </Breadcrumb>
                    </Col>
                </Row>
                <Card style="padding: 0">
                    <div>
                        <Table @on-row-click="push" :loading="loading" border stripe :columns="columns1" :data="data1"
                               style="height: 520px;"></Table>
                    </div>
                    <Row type="flex" justify="end" class="code-row-bg">
                        <Col style="margin: 18px 15px 0  0;">
                        共<span style="color: #D72236;font-size: 18px;font-weight: 700"> {{Untreateda}} </span>条数据，有<span style="color: #D72236;font-size: 18px;font-weight: 700"> {{Untreated}} </span>条待处理订单
                        </Col>
                        <Page @on-change="pageCli" :total="total" :page-size="10" :current="cuent"
                              style="margin-top: 16px;"></Page>
                    </Row>
                </Card>
            </Content>
            <Icon type="ios-checkmark-empty"></Icon>
            <Footer class="layout-footer-center" style="text-align: center">2011-2018 &copy; 怡乐仓库</Footer>
        </Layout>
    </div>
</template>
<script>
    export default {
        name: 'Inco',
        data () {
            return {
                user:{},
                cuent: 1,
                loading: true,
                total: 1,
                time: '',
                model1: '送货单',//下拉框值
                associate_noY:'',
                sheet_no:'',
                publisher_no:'',
                SearchCon: '',
                Check: '',
                Untreated:'',
                Untreateda:'',
                cityList: [
                    {
                        value: '送货单',
                        label: '送货单'
                    },
                    {
                        value: '入库单',
                        label: '入库单'
                    },
                    {
                        value: '供应商',
                        label: '供应商'
                    }
                ],
                columns1: [
                    {
                        title: '送货单',
                        key: 'associate_noY'
                    },
                    {
                        title: '入库单',
                        key: 'sheet_no'
                    },
                    {
                        title: '供应商',
                        key: 'publisher_no',
                    },
                    {
                        title: '品种',
                        key: 'pz_nums'
                    },
                    {
                        title: '收货时间',
                        key: 'dt',
                        render:function(h,i){
                            return  i.row.dt.split('T')[0]
                        }
                    },
                    {
                        title: '收货人',
                        key: 'chn_name',
                    },
                    {
                        title: '状态',
                        key: 'status',
                        render:function(h,i){
                            var sta=''
                            if(i.row.status=='0'){
                                sta= <p style="color:#ed3f14">待上架</p>
                            }else if(i.row.status=='1'){
                                sta= <p style="color:#ff9900">部分上架</p>
                            }else if(i.row.status=='2'){
                                sta= <p style="color:#666">上架完成</p>
                            }
                            return sta
                        }
                    },
                    {
                        title: '留档情况',
                        key: 'status',
                        render:function(h,i){
                            var sta=''
                            if(i.row.status=='0'){
                                sta= <p class="ivu-icon ivu-icon-ios-analytics-outline" style="font-size:14px;"></p>
                            }else if(i.row.status=='1'){
                                sta= <p class="ivu-icon ivu-icon-ios-analytics" style="font-size:14px"></p>
                            }else if(i.row.status=='2'){
                                sta= <p class="ivu-icon ivu-icon-checkmark" style="font-size:14px;color:#666"></p>
                            }
                            return sta
                        }
                    }
                ],
                data1: []
            }
        },
        methods: {
            //采购单列表
            Stock(num){
                var v = this;
                v.cuent = num
                v.loading = true;
                v.Axios.post('http://116.247.103.119/imt16webapi/sheets/GetOnShelfListNew', v.Qs.stringify({
                    associate_noY: v.associate_noY,
                    sheet_no:v.sheet_no,
                    publisher_no:v.publisher_no,
                    status: v.Check,
                    pagenum: num,
                    pagesize: 10,
                    startdate: v.time[0],//开始时间
                    enddate: v.time[1],//结束时间
                })).then(function (res) {
                    if (res.data.error == 0) {
                        v.data1 = res.data.data;
                        v.total = res.data.total;
                        v.Untreated=res.data.Untreated;
                        v.Untreateda=res.data.total;
                    } else {
                        v.data1 = [];
                        v.total = 1;
                        v.Untreated='';
                        v.Untreateda='';
                    }
                    v.loading = false;
                }).catch(function(){
                    v.$emit('error','对方不想说话，并且向你抛出了一个异常')
                })
            },

            //列表点击跳转
            push(i, h){
                this.$router.push('/IncoVou?sheet_no=' + i.sheet_no + '')
            },

            //只看待收货
            Checkbox(i){
                i == true ? this.Check = 0 : this.Check = '';
                this.Stock(1);
            },

            //分页回调
            pageCli(i){
                this.Stock(i)
            },

            back(i){
                this.user =i
            },

            search(){
                var v = this;
                if (v.model1 == '送货单') {
                    v.associate_noY = v.SearchCon;
                    v.sheet_no='';
                    v.publisher_no=''
                } else if(v.model1 == '入库单') {
                    v.associate_noY = '';
                    v.sheet_no=v.SearchCon;
                    v.publisher_no=''
                }else if(v.model1 == '供应商') {
                    v.associate_noY = '';
                    v.sheet_no='';
                    v.publisher_no=v.SearchCon
                }
                v.Stock(1)
            },

            setime(i){
                this.time = i
            }
        },
        mounted(){
            this.Stock(1);
        },
        beforeCreate(){}
    }

</script>

<!-- Add "scoped" attribute to limit less to this component only -->
<style scoped lang="less">
</style>



